import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import {AuthService} from "../../login/services/auth.service";
import { DacConfig } from 'app/shared/services/dac-config.service';
import { ThemeService } from 'app/theme/services/theme.service';

@Component({
  selector: 'app-assessments',
  templateUrl: './assessments.component.html',
  styleUrls: ['./assessments.component.css']
})
export class AssessmentsComponent implements OnInit, OnDestroy {

  constructor(
    private authService:AuthService
    ,private router:Router
    ,private dacConfig:DacConfig
    ,private themeService:ThemeService
  ) { }

  ngOnInit() {
    this.themeService.setStickyFixContainerEnabled(true);
  }

  ngOnDestroy() {
    this.themeService.setStickyFixContainerEnabled(false);
  }

  public shouldShowHeader() {
    return !this.dacConfig.seamlessMode;
  }

  public get notSeamless() {
    return !this.dacConfig.seamlessMode;
  }
}
